.profile_container {
  margin: 120px 0 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 60px;

}

.navigation_section {

}

.link_container {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 80px;
  display: flex;
  flex-flow: column;
  gap: 40px;
}

.navigation_description {
  max-width: 320px;
}

.form_container {
  display: flex;
  flex-flow: column nowrap;
  gap: 24px;
}

.button_container {
  display: flex;
  justify-content: flex-end;
}

.link {
  text-decoration: none;
  border: none;
  color: rgb(133,133,173)

}

.link_active {
  text-decoration: none;
  color: rgb(255, 255, 255);

}

@media (max-width: 390px) {
  .profile_container {
    padding: 0 0 0 12px;
    box-sizing: border-box;
  } 
  .link_container {
    margin-bottom: 40px;
  }

  .link {
    font-size: 18px;
  }
}
